import React from "react"

export default function PriceCatergories(props) {
    return (
        <div className="price-categories">
            <div className="price" onClick={props.refresh}>£10</div>
            <div className="price" onClick={props.refresh}>£20</div>
            <div className="price" onClick={props.refresh}>£30</div>
            <div className="price" onClick={props.refresh}>£50+</div>
        </div>
    )
}